<template>
	<view style="height: 100vh;">
		<view class="matching-wrapper">
			<!--外圈-->
			<view class="border-outter" v-if="matchedUser==null"></view>
			<view class="border-outter-static" v-else></view>
			<!--内圈-->
			<view class="border-inner" v-if="matchedUser==null"></view>
			<view class="border-inner-static" v-else></view>
			<!--状态-->
			<view class="matching-state" v-if="matchedUser==null">
				<text>匹配中</text>
			</view>
			<view class="matching-state-static" v-else>
				<text>匹配成功</text>
			</view>
			<!--计时-->
			<view class="matching-time" v-if="matchedUser==null">
				<text>{{seconds}}s</text>
			</view>
		</view>

		<view class="result" v-if="matchedUser==null">
			<text class="result-text">没有找到合适的结果</text>
		</view>
		<view class="result" v-else>
			<navigator url="../matchedUserInfo/matchedUserInfo">
				<image src="../../static/avatar.png" class="user-avatar"></image>
			</navigator>
			<text class="user-info-text" style="top: 20%;">{{matchedUser.username}}</text>
			<text class="user-info-text" style="top: 30%; font-size: 15px;">{{matchedUser.gender==1?'男':'女'}}</text>
		</view>
		<button class="start-chat-button" type="primary" v-if="matchedUser!=null">开聊</button>
		<text class="exit-link" v-if="matchedUser==null" @click="giveup()">放弃</text>
		<text class="exit-link" v-else @click="continueMatching()">继续探索</text>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				seconds: 0,
				matchedUser: null
				
			}
		},
		mounted() {
			let _this = this
			setInterval(function() {
				_this.seconds = _this.seconds + 1
				if(_this.seconds == 5){
					_this.matchedUser = {
						userId: 1,
						username: '张三',
						avatar: null,
						gender: 1,
					}
				}
			}, 1000)
		},
		methods: {
			giveup() {
				uni.showModal({
					title: '提示',
					content: '确定放弃本次匹配？',
					success: function (res) {
						if (res.confirm) {
							uni.navigateTo({
								url:'/pages/discover/discover'
							})
						}
					}
				});
			},

			continueMatching() {
				let _this = this
				uni.showModal({
					title:'提示',
					content:'确定重新匹配？',
					success() {
						_this.seconds = 0
						_this.matchedUser = null
					}
				})
			}
		}
	}
</script>
<style src="./matching.css"></style>
<style>
	body {
		height: 100vh;
		overflow: hidden;
	}
</style>
